.content-div{
    text-align: center;
    color: white;
    margin-top: 3rem;
}

.content-box{
    text-align: center;
    height: 10rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

.content-box:hover{
    border-top: 1rem solid white;
    background-color: #061429;
    transition: all 1s;
}

.inner-div{
    position: absolute;
}

.content-div h2{
    font-size: 3rem;
}

.content-div span{
    font-size: 2rem;
}

.button-class{
    background-color: transparent;
    color: white;
    font-size: 2rem;
    height: 3rem;
    width: 8rem;
    border: none;
}

.button-class:focus{
    outline: none;
}