body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bottom-btn{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;float:left}

.design-card{
    height: 35rem;
    border-radius: 1rem;
    z-index: 2;
    position: absolute;
    background: #A0A0A0;
    opacity: 0.3;
    -webkit-clip-path: polygon(70% 0, 50% 50%, 70% 35%, 30% 100%, 45% 55%, 30% 65%, 70% 0);
            clip-path: polygon(70% 0, 50% 50%, 70% 35%, 30% 100%, 45% 55%, 30% 65%, 70% 0);
}

.design-card::before{
    content: '';
    position: absolute;
    height: 20%;
    width: 100%;
    top: 200%;
    background: linear-gradient(
        120deg, 
        transparent,
        white,
        transparent
        );
    transition: all 1s;
}

.design-card:hover::before{
    top:-100%;
}

.card-box{
    margin: 0 auto;
    background: #061429;
    color: white;
    border-radius: 1rem;
    padding: 1rem;
    z-index: 1;
}

.card-body::before{
    color: transparent;
}

.card-header p{
    float: right;
    font-size: 2rem;
    text-align: right;
}

.dot{
    height: 6rem;
    width: 6rem;
    background: transparent;
    border-radius: 50%;
    border: 2px solid yellow;
    text-align: center;
    line-height: 5.5rem;
    font-size: 2rem;
}

h1{
    color: white;
    margin-left: 1rem;
    font-size: 9rem;
}

.horizontal-arrow{
    height: 6rem;
    position: relative;
}

.horizontal-arrow span{
    position: absolute;
    top: 7%;
    font-size: 3rem;
}
.content-div{
    text-align: center;
    color: white;
    margin-top: 3rem;
}

.content-box{
    text-align: center;
    height: 10rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

.content-box:hover{
    border-top: 1rem solid white;
    background-color: #061429;
    transition: all 1s;
}

.inner-div{
    position: absolute;
}

.content-div h2{
    font-size: 3rem;
}

.content-div span{
    font-size: 2rem;
}

.button-class{
    background-color: transparent;
    color: white;
    font-size: 2rem;
    height: 3rem;
    width: 8rem;
    border: none;
}

.button-class:focus{
    outline: none;
}
.navbar {
  font-size: 18px;
  background: black;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 10px;
}

.main-nav {
  list-style-type: none;
  display: none;
  width: 100%;
  padding-left: 0;
  transition: display 4s;
}

.nav-links {
  text-decoration: none !important;
  color: rgba(255, 255, 255, 0.7);
}

.dashboard-image {
  text-align: center;
  width: 100%;
  position: inherit;
}

.dashboard-image img{
    height: 8rem;
    width: 25rem;
  }

.main-nav li {
  text-align: center;
  margin: 15px auto;
}

.navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
}

.active {
  display: block;
}

.dashboard-box{
  margin: 0 auto;
  margin-top: 5rem;
}

@media screen and (min-width: 768px) {

  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 70px;
    align-items: center;
  }

  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end;
  }

  .main-nav li {
    margin: 0;
  }

  .nav-links {
    margin-left: 40px;
    text-decoration: none !important;
  }

  .navbar-toggle {
    display: none;
  }

  .dashboard-image {
    position: absolute;
    top: 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

  .dashboard-image img {
    height: 4rem;
    width: 15rem;
  }

  .nav-links:hover {
    color: rgba(255, 255, 255, 1);
  }

  .dashboard-image img{
    height: 8rem;
    width: 25rem ;
  }
}

