.design-card{
    height: 35rem;
    border-radius: 1rem;
    z-index: 2;
    position: absolute;
    background: #A0A0A0;
    opacity: 0.3;
    clip-path: polygon(70% 0, 50% 50%, 70% 35%, 30% 100%, 45% 55%, 30% 65%, 70% 0);
}

.design-card::before{
    content: '';
    position: absolute;
    height: 20%;
    width: 100%;
    top: 200%;
    background: linear-gradient(
        120deg, 
        transparent,
        white,
        transparent
        );
    transition: all 1s;
}

.design-card:hover::before{
    top:-100%;
}

.card-box{
    margin: 0 auto;
    background: #061429;
    color: white;
    border-radius: 1rem;
    padding: 1rem;
    z-index: 1;
}

.card-body::before{
    color: transparent;
}

.card-header p{
    float: right;
    font-size: 2rem;
    text-align: right;
}

.dot{
    height: 6rem;
    width: 6rem;
    background: transparent;
    border-radius: 50%;
    border: 2px solid yellow;
    text-align: center;
    line-height: 5.5rem;
    font-size: 2rem;
}

h1{
    color: white;
    margin-left: 1rem;
    font-size: 9rem;
}

.horizontal-arrow{
    height: 6rem;
    position: relative;
}

.horizontal-arrow span{
    position: absolute;
    top: 7%;
    font-size: 3rem;
}