.navbar {
  font-size: 18px;
  background: black;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 10px;
}

.main-nav {
  list-style-type: none;
  display: none;
  width: 100%;
  padding-left: 0;
  transition: display 4s;
}

.nav-links {
  text-decoration: none !important;
  color: rgba(255, 255, 255, 0.7);
}

.dashboard-image {
  text-align: center;
  width: 100%;
  position: inherit;
}

.dashboard-image img{
    height: 8rem;
    width: 25rem;
  }

.main-nav li {
  text-align: center;
  margin: 15px auto;
}

.navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
}

.active {
  display: block;
}

.dashboard-box{
  margin: 0 auto;
  margin-top: 5rem;
}

@media screen and (min-width: 768px) {

  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 70px;
    align-items: center;
  }

  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end;
  }

  .main-nav li {
    margin: 0;
  }

  .nav-links {
    margin-left: 40px;
    text-decoration: none !important;
  }

  .navbar-toggle {
    display: none;
  }

  .dashboard-image {
    position: absolute;
    top: 1rem;
    width: fit-content;
  }

  .dashboard-image img {
    height: 4rem;
    width: 15rem;
  }

  .nav-links:hover {
    color: rgba(255, 255, 255, 1);
  }

  .dashboard-image img{
    height: 8rem;
    width: 25rem ;
  }
}
